import React, { useState, useEffect } from 'react';

import Person from './components/Person';
import PersonForm from './components/PersonForm';
import Filter from './components/Filter';
import Notification from './components/Notification';
import personServices from './services/persons';

const App = () => {
  const [persons, setPersons] = useState([]);
  const [newName, setName] = useState('');
  const [newNumber, setNumber] = useState('');
  const [showPersons, setShowPersons] = useState('');
  const [notification, setNotification] = useState({
    message: null
  });

  useEffect(() => {
    personServices.getAll().then(persons => setPersons(persons));
  }, []);

  const notify = (message, type = 'success') => {
    setNotification({ message, type });
    setTimeout(() => setNotification({ message: null }), 10000);
  };

  const addPerson = event => {
    event.preventDefault();

    const alreadyExist = persons.find(person => person.name === newName.trim());

    if (!alreadyExist) {
      const newPerson = {
        name: newName,
        number: newNumber
      };
      personServices
        .create(newPerson)
        .then(returnedPerson => {
          setPersons(persons.concat(returnedPerson));
          notify(`Added ${newPerson.name}`);
        })
        .catch(error => {
          notify(error.response.data.error, 'error');
        });
    } else {
      const thisPerson = persons.find(person => person.name === newName.trim());

      if (thisPerson.number !== newNumber) {
        if (
          window.confirm(
            `${newName} is already added to phonebook, replace the old number with a new one?`
          )
        ) {
          const newPerson = { ...thisPerson, number: newNumber };
          personServices
            .update(thisPerson.id, newPerson)
            .then(updatedPerson => {
              setPersons(
                persons.map(person =>
                  person.id !== thisPerson.id ? person : updatedPerson
                )
              );

              notify(`Updated number for ${newPerson.name}.`);
            });
        }
      } else {
        notify(
          `Given phonenumber for ${newName} is already in the phonebook.`,
          'error'
        );
      }
    }

    setName('');
    setNumber('');
  };

  const deletePerson = ({ name, id }) => {
    if (window.confirm(`Delete ${name} ?`)) {
      personServices
        .remove(id)
        .then(status => {
          if (status === 204) {
            setPersons(persons.filter(person => person.id !== id));

            notify(`Information of ${name} is removed from server.`, 'error');
          }
        })
        .catch(error => {
          setPersons(persons.filter(person => person.id !== id));
          notify(
            `Information of ${name} has already been removed from server.`,
            'error'
          );
        });
    }
  };

  const filterPerson = event => {
    setShowPersons(event.target.value);
  };

  const handleNameChange = event => {
    setName(event.target.value);
  };

  const handleNumberChange = event => {
    setNumber(event.target.value);
  };

  const personsList = () => {
    const filtered = persons.filter(person => {
      return person.name.toLowerCase().includes(showPersons.toLowerCase());
    });

    const toDisplay = filtered.map(person => (
      <Person
        key={person.id}
        name={person.name}
        number={person.number}
        deletePerson={() => deletePerson(person)}
      />
    ));

    return toDisplay;
  };

  return (
    <div>
      <h2>Phonebook</h2>

      <Notification notification={notification} />

      <Filter filterPerson={filterPerson} />

      <h3>add a new</h3>

      <PersonForm
        addPerson={addPerson}
        handleNameChange={handleNameChange}
        handleNumberChange={handleNumberChange}
        newName={newName}
        newNumber={newNumber}
      />

      <h3>Numbers</h3>

      {personsList()}
    </div>
  );
};

export default App;
